html{
    scroll-behavior: smooth;
}
main{
    min-height: 100vh;
}
.ItemListContainer, .h1{
    margin-top: 100px;
}
.ItemListContainer .a{
cursor: pointer;
font-size: 3rem;
}
.ItemListContainer .a:hover{
    color: #0b7052;
}
.cart{
    margin-top: 100px;
}
.cart .a{
    display: block;
}
.cart img{
    object-fit: cover;
}